import { Button, ButtonProps, Text } from '@chakra-ui/react'
import * as React from 'react'

export interface IProps extends ButtonProps {
	color: string
	onClick?: () => void
	children?: string
}
export default (props: IProps) => {
	const { color, backgroundColor, children, onClick, ...rest } = props
	return (
		<Button
			// style={{ color }}
			onClick={() => onClick()}
			borderRadius={12}
			size='md'
			minH='35px'
			minW='200px'
			backgroundColor='#fcfa3b'
		>
			{children && <Text fontSize='3xl'>{children}</Text>}
		</Button>
	)
}
